<template>
    <div class="home">
        <van-search
            v-model="value"
            show-action
            placeholder="请输入搜索关键词或小说名称"
            @search="onSearch"
            >
            <div slot="action" @click="onSearch">搜索</div>
        </van-search>
        <van-card
            v-for="(item, index) in list"
            :key="index"
            :desc="`最新章节：${item.newtitle}`"
            :title="item.title"
            :thumb="bgurl"
        >
            <div slot="tags">
                <van-tag plain type="danger">作者 - {{item.author}}</van-tag>
            </div>
        </van-card>

    </div>
</template>

<script>
import Vue from 'vue';
import { Search , Card, tag} from 'vant';
Vue.use(Search).use(tag).use(Card);
export default {
    name: "Home",
    data() {
        return {
            value: '',
            bgurl: require('../assets/bj.jpg'),
            list: []
        }
    },
    methods: {
        onSearch(){
            this.$http.get(`/api/queryName?name=${this.value}`).then(({ data })=> {
                this.list = data;
                console.log(data)
            })
        }
    },
};
</script>

